.program {
  padding: 28px 0 32px 0;
  font-family: $font-oswald;
  font-style: normal;
  color: $basic_black;
}

.program__title {
  margin: 0;
  margin-bottom: 36px;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;

  @media (min-width: $tablet-width) {
    font-size: 60px;
    line-height: 60px;
  }
}

.program__sub-title {
  margin: 0;
  margin-bottom: 38px;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    margin-bottom: 66px;
    font-size: 20px;
    line-height: 30px;
  }
}
